@import "./variables";
html {
  margin: 0px;
}
body {
  overflow-y: auto;
  overflow-x: hidden;
  position: absolute;
  margin: 0px;
  width: 100vw;
}
.top-menu {
  position: sticky;
  top: 0px;
  z-index: 1000;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 104px;
  width: 100%;
  background-color: white;
  @include mobile {
    height: 90px;
    justify-content: space-between;

    box-sizing: border-box;
  }
}

.top-menu > img {
  height: 42px;
  width: 173px;
  position: relative;
  margin-left: 50px;
  @include mobile {
    margin-left: 17px;
    height: 42px;
    width: 135px;
  }
}
.top-menu > span {
  position: relative;
  margin-left: 54px;
  font-size: small;
  cursor: pointer;
  // font-weight: bold;
  @include mobile {
    display: none;
  }
}
.menu-icon {
  display: none;
  @include mobile {
    display: block;
    color: black;
    margin-right: 20px;
  }
}

.menu-list {
  border-style: solid;
  border-width: 2px 0px 0px 0px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;

  padding-top: 20px;
}
.menu-list-item {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 10px;
  padding-top: 10px;
  font-size: 20px;
  font-weight: bolder;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu-list-item:active {
  background-color: rgb(170, 152, 152);
}
.beauty,
.fitness {
  position: relative;
  height: 590px;
  width: 100vw;
  background-image: url(../pictures/keira-knightley.jpg);
  background-repeat: no-repeat;
  background-size: 950px;
  box-sizing: border-box;
  background-color: black;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
  animation: mymove 3s;
  @keyframes mymove {
    from {
      background-size: 965px;
    }
    to {
      background-size: 950px;
    }
  }
  @include mobile {
    // width: 120vw;
    height: 340px;
    background-size: 550px;
    background-position-x: center;
    @keyframes mymove {
      from {
        background-size: 565px;
      }
      to {
        background-size: 550px;
      }
    }
  }
}
.fitness {
  animation: my-move 3s;

  background-image: url(../pictures/ballerina-3055155.jpg);
  background-size: 1600px;
  @keyframes my-move {
    from {
      background-size: 1535px;
    }
    to {
      background-size: 1600px;
    }
  }
  @include mobile {
    background-size: 550px;
    @keyframes my-move {
      from {
        background-size: 600px;
      }
      to {
        background-size: 550px;
      }
    }
  }
}
.fitness-container {
  height: 590px;
  background-color: white;
  @include mobile {
    height: 340px;
  }
}
.test {
  height: 590px;
  width: 680px;
  position: relative;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
}

.insider {
  width: 100vw;
  height: fit-content;
  margin-top: -200px;
  background-color: transparent;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: auto;
  padding-bottom: 40px;
  margin-bottom: 50px;
  @include mobile {
    margin-top: -60px;
  }
}

.insider::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  padding-top: 40px;
}

.insider::-webkit-scrollbar-track {
  background: lightgray;
  border-radius: 10px;
  margin-left: 513px;
  margin-right: 60px;
}
.insider::-webkit-scrollbar-thumb {
  border-radius: 10px;
  height: 10px;
  width: 8px;
  border: none;
  background: rgb(116, 103, 103);
  background: -webkit-linear-gradient(rgb(105, 70, 70), rgb(65, 55, 55));
}

.product-container {
  width: 220px;
  box-sizing: border-box;
  height: 470px;
  margin-left: 50px;

  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 15px;
}
.product-container:hover {
  background-color: white;
  position: relative;
  box-shadow: 0px 3px 16px 2px rgba(128, 128, 128, 0.322);
  transition: width 0.5s 0.5s, height 0.5s 0.5s, background-color 0.5s;
}

.product-description {
  margin-top: 10px;
}
.product1,
.product2,
.product3,
.product4 {
  height: 250px;
  width: 50%;
  background-size: 120%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  align-items: center;
}
.product2 {
  background-image: url("../pictures/p2.png");
}
.product1 {
  background-image: url("../pictures/p1.png");
}
.product3 {
  background-image: url("../pictures/p3.png");
}
.product4 {
  background-image: url("../pictures/p4.png");
}
.dance1,
.dance2,
.dance3,
.dance4,
.dance5 {
  height: 250px;
  width: 60%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  align-items: center;
}
.dance1 {
  background-image: url("../pictures/jazz1.png");
  background-size: 96%;
}
.dance2 {
  background-image: url("../pictures/tango1.png");

  background-size: 141%;
}
.dance3 {
  background-image: url("../pictures/ballet1.png");
}
.dance4 {
  background-image: url("../pictures/zumba1.png");
  background-size: 107%;
}
.fade-in {
  flex-shrink: 0;
  width: 40vw;
  padding-left: 83px;
  box-sizing: border-box;
  padding-top: 170px;
  font-size: 40px;
  opacity: 1;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
  @include mobile {
    padding-left: 36px;
    padding-top: 0px;
  }
}
.fade-out {
  opacity: 0;
  flex-shrink: 0;
  width: 40vw;
  padding-left: 83px;
  box-sizing: border-box;
  padding-top: 170px;
  font-size: 40px;
  transition: width 2s 2s, height 2s 2s, opacity 2s;
  @include mobile {
    padding-left: 36px;
    padding-top: 0px;
  }
}
.bold-title {
  @include mobile {
    font-size: 18px;
  }
}
.lazy {
  background-color: chartreuse;
  height: 590px;
  width: 680px;
}
.overlay {
  position: fixed;
  display: block;
  overflow: auto;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background-color: transparent;
  z-index: 999;
}

.content {
  background-color: yellow;
  width: 95vw;
  height: 50vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem;
  position: relative;
  overflow: auto;
  align-self: auto;
  margin-top: 96px;
}
.footer {
  height: fit-content;
  width: 100vw;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: flex-start;

  @include mobile {
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.title-container {
  display: flex;
  height: fit-content;
  padding-top: 60px;
  padding-bottom: 60px;
  justify-content: flex-start;
  box-sizing: border-box;
  align-items: flex-start;
  flex-direction: column;
  color: snow;
  margin-left: 60px;

  @include mobile {
    height: fit-content;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-left: 30px;
    margin-right: 30px;
  }
}
.titles {
  font-size: 20px;
  margin-bottom: 35px;
}
.subtitles {
  font-size: 15px;
  margin-bottom: 10px;
}
.empty {
  width: 83px;
  height: 470px;
  background-color: transparent;
  display: flex;
  flex-shrink: 0;
}
.hair,
.Brushing,
.menu-image1,
.menu-image2 {
  height: 280px;
  width: 100%;
  background-repeat: no-repeat;
  background-position-y: bottom;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  align-items: center;
  background-size: cover;
  background-position: top;
}
.menu-image1 {
  background-image: url("../pictures/skin.jfif");
}
.menu-image2 {
  background-image: url("../pictures/eyeshadow.jpg");
}
.hair {
  background-image: url("../pictures/Hair.jpg");
}
.Brushing {
  background-image: url("../pictures/Brushing.jpg");
}
.magazine2,
.magazine1 {
  background-size: contain;
  margin: auto;
  background-position-x: center;
  background-repeat: no-repeat;
  height: 190px;
}
.magazine1 {
  background-image: url("../pictures/magazine1.jpg");
}
.magazine2 {
  background-image: url("../pictures/magazine2.jpg");
}
.product-container-menu {
  width: 220px;
  box-sizing: border-box;
  height: 400px;
  margin-left: 50px;
  margin-top: 45px;
  background-color: rgb(245, 204, 211);
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 15px;
  @include mobile {
    margin-left: 0;
  }
}
.product-container-menu:hover {
  background-color: rgb(245, 204, 211);
  position: relative;
  box-shadow: 0px 3px 16px 2px rgba(128, 128, 128, 0.322);
  transition: width 0.5s 0.5s, height 0.5s 0.5s, background-color 0.5s;
}
.product-description-menu {
  margin-top: 10px;
  margin-bottom: 50px;
  font-size: 15px;
  margin-right: 5px;
  text-align: center;
}
.makeup-menu {
  display: flex;
  justify-content: flex-start;
  @include mobile {
    flex-direction: column;
    align-items: center;
    padding-bottom: 150px;
  }
}
.hair-menu {
  display: flex;
  justify-content: space-evenly;
  padding-top: 30px;
  @include mobile {
    flex-direction: column;
    padding-bottom: 150px;
    align-items: center;
    overflow-x: hidden;
  }
}
.hair-menu-txt {
  padding-top: 45px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  @include mobile {
    margin-left: -117px;
    overflow-x: hidden;
  }
}
.magazine-menu {
  padding-left: 40px;
  display: flex;
  justify-content: flex-start;
  @include mobile {
    flex-direction: column;
    overflow-x: hidden;
    padding-bottom: 150px;
    padding-left: 0px;
    align-items: center;
  }
}
